import { Component, OnInit, Sanitizer, SecurityContext } from '@angular/core';
import { SubPageComponent } from '../sub-page/sub-page.component';
import { ApiServicesService } from '../../services/api-services.service';
import { Project } from '../../interfaces/project';
import { DomSanitizer } from '@angular/platform-browser';
import { ProjectServiceService } from 'src/app/services/project-service.service';
import { Observable, Subscription, BehaviorSubject } from 'rxjs';

@Component({
    selector: 'app-architecture',
    templateUrl: './../sub-page/sub-page.component.html',
    styleUrls: ['./../sub-page/sub-page.component.css']
})

export class ArchitectureComponent extends SubPageComponent {
    title = 'ARCHITECTURE';
    description = 'BRINGING EVERY RELEVANT DESIGN DISCIPLINE WITH OUR \
        EXPERIENCE TO CREATE THE MOST CONTEXTUAL DESIGN  THAT FIT THE PROJECT.';

    projects;
    showcases;
    contents;

    projectSub : Subscription;
    showcaseSub : Subscription;

    constructor(
        private projectService: ProjectServiceService
    ){
        super();
    }

    ngOnInit(){

        this.projectSub = this.projectService.getContent('architecture', 'project')
        .subscribe((val) => {
            this.projects = val;
            this.contents = this.projectService.generatePageArray('architecture', this.projects, this.showcases);
        });

        this.showcaseSub = this.projectService.getContent('architecture', 'showcase')
        .subscribe((val) => {
            this.showcases = val;
            this.contents = this.projectService.generatePageArray('architecture', this.projects, this.showcases);
        });

        this.projectService.refreshData();
    }

    ngOnDestroy(){
        this.projectSub.unsubscribe();
        this.showcaseSub.unsubscribe();
    }

    isLastOddMainContent(index){
        if(this.projects === undefined) return false;
        return (this.isMainContent(index) && index == this.projects.length)
    }
}
