import { Injectable } from '@angular/core';
import { ApiServicesService } from './api-services.service';
import { Project } from '../interfaces/project';
import { Showcase } from '../interfaces/showcase';
import { Subject, Observable, of, timer } from 'rxjs';
import { take } from 'rxjs/operators';

@Injectable({
    providedIn: 'root'
})

export class ProjectServiceService {
    architectureProjects : Project[];
    architectureShowcases : Showcase[];
    interiorProjects : Project[];
    interiorShowcases : Showcase[];

    architectureProjectSubject = new Subject<any[]>();
    architectureShowcaseSubject = new Subject<any[]>();
    interiorProjectSubject = new Subject<any[]>();
    interiorShowcaseSubject = new Subject<any[]>();

    constructor(
        private apiServices: ApiServicesService
    ) {
        // Set promises to load all projects and showcases json
        let promises = [];
        promises.push(this.apiServices.getProjects('architecture'));
        promises.push(this.apiServices.getProjects('interior'));
        promises.push(this.apiServices.getShowcases('architecture'));
        promises.push(this.apiServices.getShowcases('interior'));

        Promise.all(promises)
        .then(data => {
            this.architectureProjects = this.jsonToProjects(data[0]);
            this.interiorProjects = this.jsonToProjects(data[1]);
            this.architectureShowcases = this.jsonToShowcase(data[2]);
            this.interiorShowcases = this.jsonToShowcase(data[3]);
        })
        .then(() => {
            this.refreshData();
        })
        .catch((e) => {
            console.log(e);
        });
    }

    refreshData(){
        this.architectureProjectSubject.next(this.architectureProjects);
        this.architectureShowcaseSubject.next(this.architectureShowcases);
        this.interiorProjectSubject.next(this.interiorProjects);
        this.interiorShowcaseSubject.next(this.interiorShowcases);
    }

    getContent(page : String, type : String): Observable<any[]> {
        if(page === 'architecture'){
            if(type === 'project')
                return this.architectureProjectSubject.asObservable();
            else
                return this.architectureShowcaseSubject.asObservable();
        } else {
            if(type === 'project')
                return this.interiorProjectSubject.asObservable();
            else
                return this.interiorShowcaseSubject.asObservable();
        }
    }

    jsonToShowcase(data:any[]) : Showcase[]{
        let showcases : Showcase[] = [];
        data.forEach(value => {
            showcases.push(
                {
                    'type': value.type,
                    'image': value.url
                }
            );
        });
        return showcases;
    }

    jsonToProjects(data : any[]) : Project[]{
        let projects : Project[] = [];
        data.forEach(value => {
            projects.push(
                {
                    'id': value.id,
                    'title': value.title,
                    'description': value.description,
                    'overall': value.overall,
                    'mainImages': value.main_image,
                    'additionalImages': value.additional_image
                }
            );
        });
        return projects;
    }

    generatePageArray(type, main, secondary){
        let array = [];

        if(main === undefined || secondary === undefined){
            return;
        }

        // projects
        // if projects are odd => 2main 1sec 2main 1sec 1main
        //

        // For architecture and interior sub main page
        if(type === 'architecture' || type === 'interior'){

            for (let i = 0,j = 0; i < main.length; i++) {
                // Add secondary element for every 2 main element
                if(i % 2 == 0 && i != 0){
                    console.log("adding showcase " + j);
                    array.push(secondary[j]);
                    j++;
                }

                // Add main element if it's defined
                if(main[i] != undefined){
                    console.log("adding projects " + i);
                    array.push(main[i]);
                }

                //
                if(i == main.length-1 && main.length%2==0 ){
                    console.log("adding showcase " + j);
                    array.push(secondary[j]);
                    j++;
                }
            }

        }

        return array;
    }
}
