import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomepageComponent } from './components/homepage/homepage.component';
import { PageComponent } from './components/page/page.component';
import { AboutComponent } from './components/about/about.component';
import { DesignWorkComponent } from './components/design-work/design-work.component';
import { ContactComponent } from './components/contact/contact.component';
import { ArchitectureComponent } from './components/architecture/architecture.component';
import { InteriorComponent } from './components/interior/interior.component';


const routes: Routes = [
    {
        path: '',
        component: HomepageComponent
    },
    {
        path: '',
        component: PageComponent,
        children: [
            {
                path: 'design-works',
                component: DesignWorkComponent
            },
            {
                path: 'about',
                component: AboutComponent
            },
            {
                path: 'contact',
                component: ContactComponent
            },
            {
                path: 'architecture',
                component: ArchitectureComponent
            },
            {
                path: 'interior',
                component: InteriorComponent
            }
        ]
    }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
