import { Component, OnInit } from '@angular/core';
import { SubPageComponent } from '../sub-page/sub-page.component';
import { Subscription } from 'rxjs';
import { ProjectServiceService } from 'src/app/services/project-service.service';

@Component({
    selector: 'app-interior',
    templateUrl: './../sub-page/sub-page.component.html',
    styleUrls: ['./../sub-page/sub-page.component.css']
})

export class InteriorComponent extends SubPageComponent {
    title = 'INTERIOR DESIGN';
    description = 'TAILOR THE SPACE HUMBLY TO CREATE A DESIGNATED MOOD AND \
        AMBIENT. CREATING A ONE OF A KIND SPACE WITH HONEST MATERIAL.';

    projects;
    showcases;
    contents;

    projectSub : Subscription;
    showcaseSub : Subscription;

    constructor(
        private projectService: ProjectServiceService
    ){
        super();
    }

    ngOnInit(){
        this.projectSub = this.projectService.getContent('interior', 'project')
        .subscribe((val) => {
            this.projects = val;
            this.contents = this.projectService.generatePageArray('interior', this.projects, this.showcases);
        });

        this.showcaseSub = this.projectService.getContent('interior', 'showcase')
        .subscribe((val) => {
            this.showcases = val;
            this.contents = this.projectService.generatePageArray('interior', this.projects, this.showcases);
        });

        this.projectService.refreshData();
    }

    ngOnDestroy(){
        this.projectSub.unsubscribe();
        this.showcaseSub.unsubscribe();
    }

    isLastOddMainContent(index){
        if(this.projects === undefined) return false;
        return (this.isMainContent(index) && index == this.projects.length)
    }
}
