import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, NavigationStart, NavigationEnd } from '@angular/router';
import { ApiServicesService } from './../../services/api-services.service';

import {Project} from './../../interfaces/project';
import { ProjectServiceService } from 'src/app/services/project-service.service';
import { Subscription } from 'rxjs';


@Component({
  selector: 'app-navigator',
  templateUrl: './navigator.component.html',
  styleUrls: ['./navigator.component.css']
})

export class NavigatorComponent implements OnInit {
    isMainNavHover = false;
    isSubNavHover = false;
    isArchNavHover = false;
    isInteriorNavHover = false;
    url;

    architectProjects : Project[];
    interiorProjects : Project[];

    ArchitectProjectSub : Subscription;
    InteriorProjectSub : Subscription;

    constructor(
        private router: Router,
        private projectService: ProjectServiceService
    ) {
        this.router.events.subscribe(e => {
            if(e instanceof NavigationEnd){
                this.url = e.url;
            }
        });

        this.ArchitectProjectSub = this.projectService.getContent('architecture', 'project')
        .subscribe((val) => {
            this.architectProjects = val;
        });

        this.InteriorProjectSub = this.projectService.getContent('interior', 'project')
        .subscribe((val) => {
            this.interiorProjects = val;
        });
    }

    ngOnInit() {
    }

    mainNavHover(){
        this.isMainNavHover = !this.isMainNavHover;
    }

    subNavHover(){
        this.isSubNavHover = !this.isSubNavHover;
    }

    archNavHover(){
        this.isArchNavHover = !this.isArchNavHover;
    }

    interiorNavHover(){
        this.isInteriorNavHover = !this.isInteriorNavHover;
    }

    isSubNavVisible(){
        if(this.isMainNavHover || this.isSubNavHover || this.isArchNavHover || this.isInteriorNavHover){
            return true;
        } else {
            return false;
        }
    }

    isArchNavVisible(){
        return this.isArchNavHover;
    }

    isInteriorNavVisible(){
        return this.isInteriorNavHover;
    }

    isMainNavActive(type){
        if(this.url === '') return false;

        if (this.url === '/' + type){
            return true;
        }

        return false;
    }
}
