import { ElementRef, Directive, Renderer2, HostBinding, Input, AfterViewInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

@Directive({
  selector: '[appDeferLoad]'
})
export class DeferLoadDirective implements AfterViewInit{
    @HostBinding('style.backgroundImage') srcAttr = null;
    @Input() imgSrc: string;

    placeholderImage = '/assets/data/1000-1000.png';

    constructor(
        private el: ElementRef,
        private sanitizer: DomSanitizer,
        private renderer: Renderer2
    ) {}

    ngAfterViewInit() {
        // this.srcAttr = this.sanitizer.bypassSecurityTrustStyle('url("' +  this.placeholderImage + '")');
        this.canLazyLoad() ? this.lazyLoadImage() : this.loadImage();
    }

    private canLazyLoad() {
        return window && 'IntersectionObserver' in window;
    }

    private lazyLoadImage() {
        const obs = new IntersectionObserver(entries => {
            entries.forEach(({ isIntersecting }) => {
                if (isIntersecting) {
                    this.loadImage();
                    obs.unobserve(this.el.nativeElement);
                }
            });
        });
        obs.observe(this.el.nativeElement);
    }

    private loadImage() {
        this.srcAttr = this.sanitizer.bypassSecurityTrustStyle('url("' +  this.imgSrc + '")');
        this.renderer.addClass(this.el.nativeElement, 'show');
    }
}